<template>
  <transition name="slide-left">
    <div class="food" v-show="isShow" ref="foodScroll">
      <div class="food-content">
        <div class="food-header">
          <div class="img-wrapper"><img :src="food.image" alt=""></div>
          <div class="back" @click="hide">
            <i class="icon-arrow_lift"></i>
          </div>
          <div class="content">
            <h1 class="title">{{food.name}}</h1>
            <div class="detail">
              <span class="sell-count">月售{{food.sellCount}}份</span>
              <span class="rating">好评率{{food.rating}}%</span>
            </div>
            <div class="price">
              <span class="now">￥{{food.price}}</span><span class="old" v-show="food.oldPrice">￥{{food.oldPrice}}</span>
            </div>
            <div class="count-wrapper">
              <count :food="food" @addEvent="_addFood"></count>
            </div>
            <transition name="fade">
              <div class="buy" v-show="!food.count || food.count === 0" @click="addFirst">加入购物车</div>
            </transition>
          </div>
        </div>
        <split v-show="food.info"></split>
        <div class="info" v-show="food.info">
          <h1 class="title">商品信息</h1>
          <p class="text">{{food.info}}</p>
        </div>
        <split></split>
        <div class="food-ratings border-1px">
          <h1 class="title">商品评价</h1>
          <ratingfilter
            :rating-type="ratingType"
            :only-content="onlyContent"
            :desc="desc"
            :ratings="food.ratings"
            @select="selectChange"
            @toggle="toggleContent"
          ></ratingfilter>
        </div>
        <div class="ratings-wrapper">
          <ul v-show="food.ratings && food.ratings.length">
            <li v-for="item in food.ratings" v-show="selectShow(item.rateType,item.text)"
                class="rating-item border-1px">
              <div class="time">{{item.rateTime | formatDate }}</div>
              <div class="content">
                <span :class="[{'icon-thumb_up':item.rateType===0},{'icon-thumb_down':item.rateType===1}]"></span>
                <span class="text">{{item.text}}</span>
              </div>
              <div class="user">
                <span class="name">{{item.username}}</span><img :src="item.avatar" width="12" height="12"
                                                                class="avatar"></span>
              </div>
            </li>
          </ul>
          <div v-show="!food.ratings || !food.ratings.length" class="no-rating">暂无评价</div>
        </div>
      </div>
    </div>
  </transition>
</template>

<script type="text/ecmascript-6">
  const ALL = 2;
  //  const POSITIVE = 1;
  //  const NWGATIVE = 0;
  import Vue from 'vue';
  import {formatDate} from '../../common/js/date';
  import count from '../count/count';
  import split from '../split/split';
  import ratingfilter from '../ratingfilter/ratingfilter';
  import BScroll from 'better-scroll';

  export default {
    props: {
      food: Object
    },
    data() {
      return {
        isShow: false,
        onlyContent: false,
        ratingType: ALL,
        desc: {
          all: '全部',
          positive: '推荐',
          negative: '吐槽'
        }
      };
    },
    filters: {
      formatDate(time) {
        let date = new Date(time);
        return formatDate(date, 'yyyy-MM-dd hh:mm');
      }
    },
    components: {
      count, split, ratingfilter
    },
    computed: {},
    methods: {
      selectShow(rateType, text) {
        if (this.onlyContent && !text) {
          return false;
        }
        if (this.ratingType === ALL) {
          return true;
        } else {
          return rateType === this.ratingType;
        }
      },
      hide() {
        this.isShow = false;
      },
      show() {
        this.isShow = true;
        this.selectType = ALL;
        this.onlyContent = false;
        this.$nextTick(() => {
          if (!this.scroll) {
            this.scroll = new BScroll(this.$refs.foodScroll, {
              click: true
            });
          } else {
            this.scroll.refresh();
          }
        });
      },
      addFirst(event) {
        if (!event._constructed) {
          return;
        }
        Vue.set(this.food, 'count', 1);
      },
      selectChange(type) {
        this.ratingType = type;
        this.$nextTick(() => {
          this.scroll.refresh();
        });
      },
      toggleContent() {
        this.onlyContent = !this.onlyContent;
        this.$nextTick(() => {
          this.scroll.refresh();
        });
      },
      _addFood(el) {
        this.$emit('addFood', el);
      }
    }
  };
</script>

